import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Select from '@material-ui/core/Select';
import FormControl from '@material-ui/core/FormControl';
import MenuItem from '@material-ui/core/MenuItem';
import InputLabel from '@material-ui/core/InputLabel';
import LinearProgress from '@material-ui/core/LinearProgress';
import Icon from '@material-ui/core/Icon';
import classes from './MoveFolders.css';
import Chip from '@material-ui/core/Chip';
import Paper from '@material-ui/core/Paper';
import TagFacesIcon from '@material-ui/icons/TagFaces';
import FaceIcon from '@material-ui/icons/Face';

class MoveFolders extends React.Component {
  state = {
    idOutFolder: null,
    idInFolder: null,
    isLoading: false,
    toRepeatValue: 0
  };
  handleChangeInput = e => {
    if (e.target.id === 'idOutFolder') {
      this.setState({ idOutFolder: e.target.value });
    }
    if (e.target.id === 'idInFolder') {
      this.setState({ idInFolder: e.target.value });
    }
  };
  handleChangeSelect = e => {
    console.log(e.target);
    this.setState({ toRepeatValue: e.target.value });
  };
  handleClick = e => {
    this.setState({ isLoading: true });
    'google'.script.run
      .withSuccessHandler(() => {
        this.setState({ isLoading: false });
      })
      .moveFoldersAndFiles(this.state.idOutFolder, this.state.idInFolder);
    'google'.script.run.setTriggerAtHours(this.state.toRepeatValue);
  };

  render() {
    return (
      <div>
        <div>
          <div>
            <TextField
              style={{ width: '100%' }}
              id="idOutFolder"
              label="Из папки"
              placeholder="Укажите URL папки"
              type="search"
              margin="normal"
              onChange={this.handleChangeInput}
            />
            {this.state.idOutFolder ? (
              <a
                href={
                  'https://drive.google.com/drive/folders/' +
                  this.state.idInFolder
                }
                style={{ fontSize: '0.7rem' }}
              >
                <Chip
                  icon={<Icon style={{ fontSize: '1rem' }}>folder_open</Icon>}
                  label={this.state.idOutFolder}
                  onClick="{handleClick}"
                  color="primary"
                  size="small"
                />
              </a>
            ) : null}
          </div>
          <div>
            <TextField
              style={{ width: '100%' }}
              id="idInFolder"
              label="В папку"
              placeholder="Укажите URL папки"
              type="search"
              margin="normal"
              icon={<Icon>add_circle</Icon>}
              onChange={this.handleChangeInput}
            />
            {this.state.idInFolder ? (
              <a
                href={
                  'https://drive.google.com/drive/folders/' +
                  this.state.idInFolder
                }
                style={{ fontSize: '0.7rem' }}
              >
                <Chip
                  icon={<Icon style={{ fontSize: '1rem' }}>folder_open</Icon>}
                  label={this.state.idInFolder}
                  onClick="{handleClick}"
                  color="primary"
                  size="small"
                />
              </a>
            ) : null}
          </div>
        </div>
        <div>
          <FormControl style={{ width: '100%', marginTop: '18px' }}>
            <InputLabel htmlFor="age-simple">Повторять</InputLabel>
            <Select
              value={this.state.toRepeatValue}
              onChange={this.handleChangeSelect}
            >
              <MenuItem value={0}>
                <em>Нет</em>
              </MenuItem>
              <MenuItem value={1}>Каждый час</MenuItem>
              <MenuItem value={2}>Раз в 3 часа</MenuItem>
              <MenuItem value={6}>Раз в 6 часа</MenuItem>
              <MenuItem value={12}>Раз в 12 часа</MenuItem>
              <MenuItem value={24}>Каждый день</MenuItem>
            </Select>
          </FormControl>
        </div>
        <div>
          <Button
            variant="outlined"
            color="primary"
            size="small"
            style={{ margin: '60px 5px' }}
            onClick={this.handleClick}
          >
            Переместить
          </Button>
          <Button
            variant="outlined"
            color="secondary"
            size="small"
            // style={{ margin: '60px 5px' }}
            onClick={this.handleClick}
          >
            Очистить
          </Button>
        </div>
      </div>
    );
  }
}
export default MoveFolders;
