import React, { Component } from 'react';
import classes from './Accordion.css';

import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoveFolders from '../MoveFolders/MoveFolders';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';

const Accordion = props => {
  const [expanded, setExpanded] = React.useState(false);
  const handleChange = panel => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  const showAndHideLoader = bool => {
    props.showAndHideLoader(bool);
  };
  // googlescripts
  const importFiles = () => {
    showAndHideLoader(true);
    'google'.script.run
      .withSuccessHandler(() => {
        showAndHideLoader(false);
      })
      .importLinks();
  };
  return (
    <div className={classes.Accordion}>
      <ExpansionPanel
        expanded={expanded === 'panel1'}
        onChange={handleChange('panel1')}
      >
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
        >
          <Typography>
            <i class="material-icons">folder</i>
            Перенос директорий
          </Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <MoveFolders />
        </ExpansionPanelDetails>
      </ExpansionPanel>
      <ExpansionPanel
        expanded={expanded === 'panel2'}
        onChange={handleChange('panel2')}
      >
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2bh-content"
          id="panel2bh-header"
        >
          <Typography className={classes.heading}>
            <i class="material-icons">file_copy</i> Импорт файлов
          </Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <List
            component="nav"
            className={classes.root}
            aria-label="Mailbox folders"
          >
            <ListItem button onClick={importFiles}>
              <i class="material-icons">link</i>
              <ListItemText primary="Импорт ссылок" />
            </ListItem>
            <Divider />
            <ListItem button divider>
              <i class="material-icons">insert_drive_file</i>
              <ListItemText primary="Файл импорта" />
            </ListItem>
            <ListItem button>
              <i class="material-icons">group_work</i>
              <ListItemText primary="Асана" />
            </ListItem>
            <Divider light />
            <ListItem button>
              <i class="material-icons">clear</i>
              <ListItemText primary="Очистить таблицу" />
            </ListItem>
          </List>
        </ExpansionPanelDetails>
      </ExpansionPanel>
    </div>
  );
};

export default Accordion;
